/** Media Queries **/
.formulario {
  width: 100%;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}

.boton {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #0a85c3;
  cursor: pointer;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones p {
  font-size: 1.6rem;
  margin-bottom: 2.5rem;
}
.acciones a {
  color: #0da6f3;
  align-self: center;
}
.acciones .redireccionar {
  color: #FFFFFF;
  background-color: #0da6f3;
  padding: 1rem 2rem;
  border-radius: 1rem;
}

.alerta {
  padding: 1rem;
  width: fit-content;
  border-radius: 1rem;
  font-weight: 700;
  margin: 2rem auto;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}